.navbar {
  margin: 0 !important;
  padding: 0 !important;
  width: 100%;
  background-color: #323031;
  font-family: "Titillium Web", sans-serif;
}
/* signup option at top */
.navbar__login__separate__block {
  background-color: var(--red);
  width: 100%;
  display: none;
}
#separate__signup {
  width: fit-content;
  margin-left: auto;
  margin-right: 30px;
  padding-top: 5px;
  padding-bottom: 5px;
}

#separate__dropdown {
  background-color: var(--red);
}
.separate__icon {
  color: white !important;
  font-size: 35px !important;
}
#separate__signup > span:hover {
  color: white;
  opacity: 0.8;
  cursor: pointer;
}
/* Main Navbar */
.navbar__block {
  height: 90px;
  width: 87%;
  padding-top: 13px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: 0.2fr 1fr;
}

.logo__image {
  width: 180px;
}
.navbar__right {
  width: 100%;
  display: grid;
  grid-template-columns: auto auto;
  align-items: center;
}
.nav__menu {
  display: flex;
  list-style: none;
  align-items: center;
  text-align: center;
  height: 100%;
}
.nav__links {
  color: white;
  text-decoration: none;
  padding: 0 10px;
  font-size: 17px;
}

.nav__links:hover {
  color: var(--red);
}

.dropdown {
  outline: none;
  border: none;
  background-color: #323031;
  color: white;
  margin-right: 5px;
}
.signup {
  margin-left: auto;
  display: flex;
  color: white;
  align-items: center;
}
h5 {
  font-size: 17px !important;
  padding-top: 5px;
  padding-right: 5px;
}
h5:hover {
  opacity: 0.8;
}
h6 {
  padding-top: 5px;
  padding-right: 3px;
}
h6:hover {
  color: var(--red);
}

.signup__icon {
  color: var(--red) !important;
  font-size: 35px !important;
}

/* mobile menu icon */
.mobile-menu-icon {
  cursor: pointer;
  position: absolute;
  top: 80px;
  right: 30px;
  display: none;
}

.mobile-menu-icon > span {
  margin-bottom: 4px;
  display: block;
  background-color: #fff;
  width: 28px;
  height: 3px;
  opacity: 0.7;
}

/* vertical navbar */
.vertical__navbar {
  display: none;
}

@media screen and (max-width: 993px) {
  .mobile-menu-icon {
    display: block;
  }
  .navbar__login__separate__block {
    display: block;
  }
  .navbar__right {
    display: none;
  }

  .vertical__navbar {
    display: flex;
    flex-direction: column;
    display: none;
    width: 100%;
  }
  .vertical__login {
    display: flex;
    background-color: var(--red);
    padding: 10px 20px;
    align-items: center;
  }
  .vertical__login > h2 {
    margin-left: 10px;
    color: white;
  }
  .vertical__login > img {
    border-radius: 180%;
    width: 50px;
    height: 50px;
  }
  .vertical__createaccount > h2 {
    margin-left: 10px;
    color: white;
  }
  .vertical__createaccount {
    display: flex;
    background-color: var(--yellow);
    padding: 10px 20px;
    align-items: center;
  }
  .vertical__createaccount > img {
    width: 50px;
    height: 50px;
  }
  .vertical__options {
    background-color: var(--light);
    display: flex;
  }
  .vertical__nav__menu {
    width: 100%;
  }
  .vertical__nav__menu li > a {
    text-decoration: none;
    margin-left: 10px;
    color: #000;
  }
  .vertical__nav__menu > li {
    padding: 5px 0;
    display: flex;
    align-items: center;
    border-bottom: 2px solid rgba(108, 117, 125, 0.15);
    padding-left: 20px;
  }
  .images__icons {
    height: 30px;
    width: 30px;
  }
  li > img {
    height: 30px;
    width: 30px;
  }
  .menu__title {
    background-color: var(--blue);
    color: #fff;
  }
  .menu__title > a {
    font-weight: 900;
  }
}
@media screen and (max-width: 500px) {
  .navbar__logo {
    height: fit-content;
    margin-top: 10px;
  }
  .logo__image {
    width: 150px;
  }
  .vertical__login > h2 {
    font-size: 25px;
    margin-top: 5px;
  }
  .vertical__createaccount > h2 {
    font-size: 25px;
    margin-top: 5px;
  }
}
