.card {
  background-image: url("https://pbs.twimg.com/profile_images/883859744498176000/pjEHfbdn.jpg");
  color: white;
  position: relative;
  transition: all 0.5s;
  height: 400px;
  margin: 40px 10px;
  border-radius: 6px;
}
.card:hover {
  cursor: pointer;
  -ms-transform: scale(1.5); /* IE 9 */
  -webkit-transform: scale(1.5); /* Safari 3-8 */
  transform: scale(1.08);
  background-size: 180%;
  transition: all 1s ease-in-out 0.5s;
}
.card__fix__block {
  display: flex;
  flex-direction: column;
  position: relative;
  top: -55px;
}
.card__logo__name > span {
  font-weight: 800;
  color: white;
  padding: 0 70px;
  font-size: 18px;
}
.card__logo {
  margin: 0 auto;
  height: 60px;
  width: 60px;
  border-radius: 180%;
  border: 1px solid white;
}

.card__logo__name {
  margin-top: -15px;
  margin-left: auto;
  margin-right: auto;
  width: max-content;
  background-color: var(--blue);
  border-radius: 0px 0px 10px 10px;
  border: 3px solid white;
}

.card > h1 {
  text-align: center;
  margin-top: -18%;
  font-size: 25px;
  font-weight: 700;
}
.card > h2 {
  text-align: center;
  /* Will be red because of :root's higher specificity */
  font-size: 25px;
  font-weight: 700;
}
.card__bottom {
  width: 100%;
  text-align: center;
  display: flex;
  justify-self: center;
  flex-direction: column;
  position: absolute;
  bottom: 10px;
}

.card__bottom__btn {
  background-color: var(--blue);
  width: fit-content;
  color: white;
  font-weight: 800;
  padding: 10px 20px;
  border-radius: 6px;
  font-size: 20px;
  margin: 0 auto;
  text-decoration: none;
}
.card__bottom__btn:hover {
  color: white;
}
@media screen and (max-width: 992px) {
  .card > h1 {
    margin-top: -15%;
  }
}
@media screen and (max-width: 500px) {
  .card {
    height: 300px;
    margin: 40px 6px;
    border-radius: 6px;
  }
  .card > h1 {
    font-size: 25px;
  }
  .card__bottom {
    font-size: small;
    position: absolute;
    bottom: 0px;
  }
  .card__bottom__btn {
    font-weight: 500;
    padding: 5px 10px;
    border-radius: 6px;
    font-size: 15px;
  }
  .card__bottom > h4 {
    font-size: 15px;
  }
}
@media screen and (max-width: 379px) {
  .card {
    margin: 40px 5px;
  }
  .card__bottom__btn {
    font-weight: 800;
    padding: 5px 10px;
  }
}
